import { FC, useState, FormEvent, ChangeEvent } from 'react';
import { useTypedDispatch } from '../../components/utils/hooks';
import {
  Input,
  Button,
} from '@ya.praktikum/react-developer-burger-ui-components';
import styles from './forgot-password-page.module.css';
import { Link, useNavigate } from 'react-router-dom';
import { forgotPassword } from '../../services/slices/user-auth/user-auth-slice';

const ForgotPassword: FC = () => {
  const [email, setEmail] = useState<string>('');
  const navigate = useNavigate();
  const dispatch = useTypedDispatch();

  // submit form
  const requestSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    try {
      await dispatch(forgotPassword(email)).unwrap();
      navigate('/reset-password');
    } catch (error: unknown) {
      console.log(error);
    }
  };

  return (
    <div className={styles.forgotpasswordpage__container}>
      <form
        onSubmit={requestSubmit}
        className={styles.forgotpasswordpage__inputs}
      >
        <h1 className="text text_type_main-medium"> </h1>
        <Input
          type={'email'}
          placeholder={' e-mail'}
          value={email}
          onChange={(e: ChangeEvent<HTMLInputElement>) =>
            setEmail(e.target.value)
          }
          required
        />
        <Button
          type="primary"
          size="medium"
          htmlType="submit"
          disabled={!email}
        >
          
        </Button>
      </form>
      <div className={styles.forgotpasswordpage__login}>
        <p> ?</p>
        <Link to="/login"></Link>
      </div>
    </div>
  );
};

export default ForgotPassword;
