import { ChangeEvent, FC, FormEvent, useEffect, useState } from 'react';
import {
  Input,
  Button,
} from '@ya.praktikum/react-developer-burger-ui-components';
import styles from './reset-password-page.module.css';
import { Link, useNavigate } from 'react-router-dom';
import {
  useTypedDispatch,
  useTypedSelector,
} from '../../components/utils/hooks';
import { selectEmailSubmited } from '../../services/slices/user-auth/user-auth-slice';
import { resetPassword } from '../../services/slices/user-auth/user-auth-slice';

const ResetPasswordPage: FC = () => {
  const [newPassword, setNewPassword] = useState<string>('');
  const [codeFromEmail, setCodeFromEmail] = useState<string>('');
  const navigate = useNavigate();
  const dispatch = useTypedDispatch();
  const isEmailSent = useTypedSelector(selectEmailSubmited);

  useEffect(() => {
    if (!isEmailSent) {
      navigate('/forgot-password');
    }
  }, [isEmailSent, navigate]);

  // submit form
  const requestSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    try {
      await dispatch(
        resetPassword({ password: newPassword, token: codeFromEmail })
      ).unwrap();
      navigate('/login');
    } catch (error: unknown) {
      console.log('Reset password failed: ', error);
    }
  };

  return (
    <div className={styles.resetpasswordpage__container}>
      <form onSubmit={requestSubmit}>
        <div className={styles.resetpasswordpage__inputs}>
          <h1 className="text text_type_main-medium"> </h1>
          <Input
            type={'password'}
            placeholder={'  '}
            icon={'ShowIcon'}
            value={newPassword}
            onChange={(e: ChangeEvent<HTMLInputElement>) =>
              setNewPassword(e.target.value)
            }
          />
          <Input
            type={'text'}
            placeholder={'   '}
            value={codeFromEmail}
            onChange={(e: ChangeEvent<HTMLInputElement>) =>
              setCodeFromEmail(e.target.value)
            }
          />
          <Button
            type="primary"
            size="medium"
            htmlType="submit"
            disabled={!newPassword || !codeFromEmail}
          >
            
          </Button>
        </div>
      </form>
      <div className={styles.resetpasswordpage__remember_password}>
        <p> ?</p>
        <Link to="/login"></Link>
      </div>
    </div>
  );
};

export default ResetPasswordPage;
