import { request } from './request';

interface IUserData {
  name: string;
  email: string;
}

const checkReponse = <T>(res: Response): Promise<T> => {
  return res.ok ? res.json() : res.json().then((err) => Promise.reject(err));
};

type TServerResponse<T> = {
  success: boolean;
} & T;

interface IUserResponseData {
  user: {
    name?: string;
    email?: string;
    password?: number;
    token?: string;
    accessToken?: string;
    refreshToken?: string;
  };
  accessToken?: string;
  refreshToken?: string;
}

// fetch  
export const userRegisterHandler = async (
  name: string,
  email: string,
  password: string
): Promise<IUserResponseData> => {
  const response = await request('/auth/register', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({ email, password, name }),
  }).then((res) => res.json());
  if (response.success) {
    return response;
  } else {
    throw new Error(',    ');
  }
};

// fetch  
export const authUserHandler = async (
  email: string,
  password: string
): Promise<IUserResponseData> => {
  const response = await request('/auth/login', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({ email, password }),
  }).then((res) => res.json());

  if (response.success) {
    return response;
  } else {
    throw new Error('    ?!');
  }
};

//    
export const forgotPasswordRequest = async (email: string): Promise<any> => {
  try {
    const response = await request('/password-reset', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ email }),
    }).then((res) => res.json());

    if (response.success && response.message === 'Reset email sent') {
      return response;
    } else {
      throw new Error('    :(');
    }
  } catch (error) {
    console.log((error as Error).message);
  }
};

//     /reset-password/reset
export const resetPasswordHandler = async (
  password: string,
  token: string
): Promise<any> => {
  try {
    const response = await request('/password-reset/reset', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ password, token }),
    }).then((res) => res.json());

    if (response.success) {
      return response;
    } else {
      throw new Error('    :(');
    }
  } catch (error) {
    console.log((error as Error).message);
  }
};

//        
export const getUserData = async (): Promise<IUserData> => {
  return new Promise<IUserData>((resolve) => {
    setTimeout(() => {
      resolve({
        name: '',
        email: '',
      });
    }, 1000);
  });
};

type TRegreshTokenResponse = TServerResponse<{
  refreshToken: string;
  accessToken: string;
}>;

//  
export const refreshToken = () => {
  return (
    request(`/auth/token`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json;charset=utf-8',
      },
      body: JSON.stringify({
        token: localStorage.getItem('refreshToken'),
      }),
    })
      .then((res) => checkReponse<TRegreshTokenResponse>(res))
      // !!      ,  
      //  ,    fetchWithRefresh
      .then((refreshData) => {
        if (!refreshData.success) {
          return Promise.reject(refreshData);
        }
        localStorage.setItem('refreshToken', refreshData.refreshToken);
        localStorage.setItem('accessToken', refreshData.accessToken);
        return refreshData;
      })
  );
};

export const fetchWithRefresh = async <T>(url: RequestInfo, options: any) => {
  try {
    const res = await fetch(url, options);
    return await checkReponse<T>(res);
  } catch (err) {
    if ((err as { message: string }).message === 'jwt expired') {
      const refreshData = await refreshToken(); // 
      options.headers.Authorization = `Bearer ${refreshData.accessToken}`;
      const res = await fetch(url, options); // 
      return await checkReponse<T>(res);
    } else {
      return Promise.reject(err);
    }
  }
};
