import { ChangeEvent, FC, FormEvent, useState } from 'react';
import {
  Input,
  Button,
} from '@ya.praktikum/react-developer-burger-ui-components';
import styles from './login-page.module.css';
import { Link } from 'react-router-dom';
import { login } from '../../services/slices/user-auth/user-auth-slice';
import { useTypedDispatch } from '../../components/utils/hooks';

const LoginPage: FC = () => {
  const [email, setEmail] = useState<string>('');
  const [password, setPassword] = useState<string>('');
  const dispatch = useTypedDispatch();

  // submit request  
  const requestSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    try {
      await dispatch(login({ email, password })).unwrap();
    } catch (error: unknown) {
      console.log(error);
    }
  };

  return (
    <div className={styles.loginpage__container}>
      <form onSubmit={requestSubmit}>
        <div className={styles.loginpage__inputs}>
          <h1 className="text text_type_main-medium"></h1>
          <Input
            type={'email'}
            placeholder={'E-mail'}
            value={email}
            onChange={(e: ChangeEvent<HTMLInputElement>) =>
              setEmail(e.target.value)
            }
          />
          <Input
            type={'password'}
            placeholder={''}
            icon={'ShowIcon'}
            value={password}
            onChange={(e: ChangeEvent<HTMLInputElement>) =>
              setPassword(e.target.value)
            }
          />
          <Button
            type="primary"
            size="medium"
            htmlType="submit"
            disabled={!email || !password}
          >
            
          </Button>
        </div>
      </form>
      <div className={styles.loginpage__register}>
        <p> -  ?</p>
        <Link to="/register"></Link>
      </div>
      <div className={styles.loginpage__forgot_password}>
        <p> ?</p>
        <Link to="/forgot-password"> </Link>
      </div>
    </div>
  );
};

export default LoginPage;
